.root {
  height: 100vh;
  background: #d00342 url('./opera-bg.jpg') no-repeat center / cover;
  font-family: 'Roboto', sans-serif;
  color: white;
  line-height: 20px;
  padding: 20px 30px;
}

.bm-logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.bm-moto {
  font-size: 11px;
  padding-top: 3px;
}

.title {
  font-size: 20px;
  line-height: 25px;
  padding-top: 47px;
  font-weight: 600;
}

.button {
  font-size: 16px;
  width: 240px;
  height: 50px;
  border: 1px solid white;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 30px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 10px;
}

.button:active {
  background-color: rgba(255, 255, 255, 0.3);
}

.text {
  font-size: 14px;
  padding-top: 40px;
}

.country {
  font-size: 16px;
  padding-top: 20px;
}

.right-icon {
  padding-left: 5px;
}



